import { createGlobalStyle } from 'styled-components'

const GlobalStyle = createGlobalStyle`
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        font-family: 'Open Sans Condensed', sans-serif;
    }

    html, body {
        overflow-x: hidden;
        background-color: #000000;
    }

    section.true {
        position: fixed;
    }
`;

export default GlobalStyle;